body {
    font-family: 'Veranda', sans-serif;
  }
  span {
    font-size: 22px;
    cursor: pointer
  }
  .overlay {
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #001536;
    overflow-x: hidden;
    transition: 0.5s;
  }
  .overlay-content {
    position: relative;
    top: 30%;
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
  .overlay a {
    padding: 10px;
    text-decoration: none;
    font-size: 32px;
    color: #7792a3;
    display: block;
    transition: 0.4s;
  }
  .overlay a:hover,
  .overlay a:focus {
    color: #eeeeee;
  }
  .overlay .closed {
    position: absolute;
    top: 40px;
    right: 35px;
    font-size: 120px;
  }
  @media screen and (max-height: 450px) {
    .overlay a {
      font-size: 20px
    }
    .overlay .closed {
      font-size: 40px;
      top: 15px;
      right: 35px;
    }
  }
body {
    height: 100%;
    text-align: center;
    background-color:#FFF;
    font-family: 'Rubik', sans-serif;
   
}
.wrapper{
    min-height: 80%;
    margin: 90px ;
}

.title{
    text-transform: uppercase;
    letter-spacing: 3px;
    font-size: 36px;
    line-height: 48px;
    padding-bottom: 48px;
}
.input{
    width: 32%;
    height: 36px;
    margin: 40px

}

.inputfull{
    width: 70%;
    height: 36px;
    margin: 40px
    
}
@media screen and (max-width: 992px) {
    .input{
        width: 100%;
        height: 36px;
        align-items: center;
    }
    
    .inputfull{
        width: 100%;
        height: 36px;
        align-items: center;
    }
  }
  
  /* On screens that are 600px or less, set the background color to olive */
  @media screen and (max-width: 600px) {
    .input{
        width: 100%;
        height: 36px;
    
    }
    
    .inputfull{
        width: 100%;
        height: 36px;
        
    }
  }
